import React, { useRef, useState } from 'react';
import emailjs from '@emailjs/browser';

import border_dark from '../../../img/border_dark.svg';
import border_blue from '../../../img/border_blue.svg';

import './Contact.css';

const Contact = () => {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');
  const [sending, setSending] = useState(false);
  const [sent, setSent] = useState(false);
  const [error, setError] = useState(false);
  const [errorMsg, setErrorMsg] = useState('');

  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();
    setSending(true);
    emailjs.sendForm('service_q3stgbj', 'template_hb21y1x', form.current, 'ez1gnij5WiOdzyc5t').then(
      (result) => {
        console.log(result.text);
        setSending(false);
        setSent(true);
      },
      (error) => {
        console.error(error.text);
        setError(true);
        setErrorMsg(error.text);
      }
    );
  };

  const renderForm = () => {
    if (sending) {
      return (
        <div id="sending-mail">
          <i className="spinner icon"></i>
        </div>
      );
    }
    if (sent && !error && !sending) {
      return (
        <div className="ui positive message">
          <div className="header">Message sent succesfully.</div>
        </div>
      );
    } else {
      return (
        <>
          <p>Feel free to leave a message.</p>
          {!error || (
            <div className="ui negative message">
              <div className="header">{errorMsg}</div>
            </div>
          )}
          <form className="ui form" id="contact-form" ref={form} onSubmit={sendEmail}>
            <div className="field">
              <input
                id="name"
                name="name"
                type="text"
                placeholder="Your name goes here"
                value={name}
                onChange={(e) => {
                  setName(e.target.value);
                }}
                style={{ borderImageSource: `url(${border_dark})` }}
              />
            </div>
            <div className="field">
              <input
                id="email"
                name="email"
                type="email"
                placeholder="Your email goes here"
                value={email}
                onChange={(e) => {
                  setEmail(e.target.value);
                }}
                style={{ borderImageSource: `url(${border_dark})` }}
              />
            </div>
            <div className="field">
              <textarea
                id="message"
                name="message"
                placeholder="And your message here"
                value={message}
                onChange={(e) => {
                  setMessage(e.target.value);
                }}
                style={{ borderImageSource: `url(${border_dark})` }}
              ></textarea>
            </div>
            <button
              className="ui blue button"
              type="submit"
              style={{ borderImageSource: `url(${border_blue})` }}
            >
              Submit
            </button>
          </form>
        </>
      );
    }
  };
  return (
    <div id="contact">
      <h1>Contact</h1>
      {renderForm()}
    </div>
  );
};

export default Contact;
